import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

const token = localStorage.getItem('retriewToken')
const objbuy = localStorage.getItem('retriewObj')
const ticketsbuy = localStorage.getItem('retriewTickets')

export default new Vuex.Store({
    state: {
        isAuth: !!token,
        token: token || null,
        objbuy: JSON.parse(objbuy || "{}"),
        ticketsbuy: JSON.parse(ticketsbuy || "[]"),
    },
    mutations: {
        retriewToken(state, token) { // мутация токена
            state.token = token
            state.isAuth = Boolean(token)
        },
        haveObj(state, objbuy) {
            state.objbuy = objbuy
            localStorage.setItem('retriewObj', JSON.stringify(objbuy))
        },
        haveTickets(state, ticketsbuy) {
            state.ticketsbuy = ticketsbuy
            localStorage.setItem('retriewTickets', JSON.stringify(ticketsbuy))
        }
    },
    actions: {
        retriewToken(context, credentials) {
            return new Promise((resolve, reject) => { //получаем токен по email/pass
                axios.post('web/login', {
                    phone: '380' + credentials.phone,
                    code: credentials.code
                })
                    .then(response => {
                        const token = response.data.result.token //беру токен - ключ
                        localStorage.setItem('retriewToken', token)
                        context.commit('retriewToken', token)
                        resolve(response)
                    })

            })
        },
        AUTH_LOGOUT(context) {
            return new Promise((resolve, reject) => {
                context.commit('retriewToken', null);
                localStorage.removeItem('retriewToken') // clear your user's token from localstorage
                resolve()
            })

        }

    },
    getters: {
        ticket: state => id => state.ticketsbuy.find( item => Number(id) === item.id)
    }
})

