<template>
  <div
    class="d-flex flex-wrap align-items-center row-finder flex-md-row flex-column  p-0 justify-content-between flex-md-nowrap flex-wrap"
  >
    <div class="d-flex align-items-center flex-md-row flex-column row-finder__places mb-2 mb-sm-0  px-3 pt-3 pb-0 p-md-0">
      <!-- <div id="to" > -->
        <v-select
          :options="Object.values(optionListCity)"
          :placeholder="placeholderFrom"
          v-model.lazy="from"
          label="city"
          class="select-city mb-md-0 mb-3"
          @search="Handler"
        >
          <template v-slot:no-options="{ search, searching }">
            <template v-if="searching">
              Не знайдено <em>{{ search }}</em>.
            </template>
            <em class="row-find-em__opacity-05" v-else>{{ defaultHandler() }}</em>
          </template>
          <template #selected-option="{city}">
