<script>
import { mapActions, mapState } from "vuex";
import RowFind from "./RowFind";

export default {
  name: "Header",
  data() {
    return {
      client: {},
      list: false,
      isArrow: false,
      isBtnActive: false,
      isLogo: false,

      activeLang: "УКР",
      menuIsVisible: false,
      mobileLangVisile: false,
    };
  },
  components: { RowFind },
  props: {
    handleOpen: {
      type: Function,
      required: true,
    },
    name: {
      type: String,
    },
  },
  computed: {    
    ...mapState(["token", "isAuth"]),
  },
  methods: {
    ...mapActions(["AUTH_LOGOUT"]),
    LOGOUT() {
      this.$store.dispatch("AUTH_LOGOUT").then(() => {
        this.$router.push("/");
      });
    },
    isBtn() {
      this.list = !this.list;
      this.isArrow = !this.isArrow;
      this.isLogo = !this.isLogo;
    },
    langChange(lang) {
      // console.log("lang change ");
      this.activeLang = lang
      this.mobileLangVisile = false
    },
    showMenu(){
      this.menuIsVisible = !this.menuIsVisible
    }
  },
  created() {
    const headers = {
      "Content-type": "application/json",
      Authorization: `Bearer ${this.token}`, //передача токена
    };
    axios
      .get("web/user", {
        headers: headers,
      })
      .then((response) => {
        this.client = response.data.result;
        // console.log("Client ",this.client);
      })
      .catch((e) => {
        if (e.response.status === 401) {
          console.log(e);
        }
      });
  },
};
</script>

