<script>
import { TheMask } from 'vue-the-mask'

export default {
    name: "Modal",
    props: {
        isOpenModal: {
            type: Boolean,
            required: true
        },
        closeModal: {
            type: Function,
            required: true
        }
    },
    components: {
        TheMask,
    },
    data() {
        return {
            login: false,
            reg: true,
            trueReg: false,

            // userNumber: '+380',
            userNumber: '',
            code: 12345,
            loading: false,
            //    modal

            // valid
            failedValidation: {
                userNumber: null
            },
            // timer
            repeatSMS: false //now will start after 4 seconds

        }
    },
    // watch: {
    //     userNumber(newNumber) {
    //         if (newNumber.length < 5) {
                // this.userNumber = '+380';
    //         }
    //     }
    // },
    methods: {
        onlyNumbers() {
            //this.failedValidation.userNumber = !/^\+[\d]{12}$/.test(this.userNumber) ? true : false;
            this.failedValidation.userNumber = (this.userNumber).length < 9 ? true : false;
            
            // this.failedValidation.userNumber = !/^[\d]{12}$/.test(this.userNumber) ? true : false;
            // console.log(this.userNumber)
            // console.log(this.failedValidation.userNumber)
        
        },
        secondReg() {
            if (!this.failedValidation.userNumber) {
                this.repeatSMS = false
                axios.post("web/register", {
                    phone: this.userNumber.slice(1,13)
                })
                    .then((res) => {
                            //show modal
                        this.trueReg = true
                        setTimeout(() => {
                            this.repeatSMS = true
                        },3000)
                    })
                    .catch(e => {
                        if (e.response.status === 404) {
                            alert('The phone has already been taken', e)
                            this.failedValidation.userNumber = true
                            this.trueReg = false
                            this.userNumber = ''
                        }
                    });
                this.userNumber = ''
            } else {
                // если проверка не пройдена
                alert('Введіть вірний телефон')

            }
        },
        isLogin() {
            this.login = true , this.reg = false
        },
        isReg() {
            this.login = false, this.reg = true
        },
        Login() {
            // console.log(this.userNumber)
            this.$store.dispatch('retriewToken', {
                // phone: this.userNumber.slice(0,13),
                phone: this.userNumber,
                code: this.code
            })
                .then(response => {
                    this.$router.push({name: 'Profile'})
                })
                .catch((e) => {
                    if (e.response.status === 401) {
                        alert('0000', e)
                    }
                })
        }
    }
}
</script>

