<script>
import Header from "./Header";
import Modal from "./Modal";
import { mapActions, mapState } from "vuex";

import vSelect from "vue-select";
import "vue-select/dist/vue-select.css";
import HomeTable from "@/views/HomeTable.vue";

import Footer from "@/views/Footer";
import RowFind from "./RowFind";

import { debounce } from "vue-debounce";

import { TheMask } from 'vue-the-mask'

export default {
  name: "Home",
  components: {
    RowFind,
    HomeTable,
    vSelect,
    Footer,
    Header,
    Modal,
    TheMask,
  },
  data() {
    return {
      listToCity: [],
      selectedCity: {
        city: "Вінниця",
        place_id: "ChIJiWRaGWVbLUcR_nTd7lnh1Ms",
      },
      optionCity: [],
      text: "",
      isOpenModal: false,
      isActiveCities: false,

      //    date and time
      date: null,
      interval: null,

      phoneNumber: '',
    };
  },
  computed: {
    ...mapState(["objbuy", "ticketsbuy"]),
  },
  mounted() {
    // this.$store.commit("haveTickets", []);
    this.getCities()
    if (!this.ticketsbuy.length) this.searchTo(this.selectedCity);
    // this.$store.commit("haveTickets", []);
  },
  beforeDestroy() {
    clearInterval(this.interval);
  },
  created() {
    this.interval = setInterval(() => {
      this.date = new Date();
    }, 1000);
  },
  methods: {
    ...mapActions(["AUTH_LOGOUT"]),
    LOGOUT() {
      this.$store.dispatch("AUTH_LOGOUT").then(() => {
        this.$router.push("/");
      });
    },
    changeCity(city) {
      this.cityArray = city;
    },
    openModal() {
      this.isOpenModal = true;
    },
    closeModal() {
      this.isOpenModal = false;
    },
    showCitiesList() {
      this.isActiveCities = !this.isActiveCities;
    },
    //пошук відправлення ЗВІДКИ

    // searchTo(search, load) {
    searchTo(search) {
      // console.log(load);
      if (search !== "") {
        // load(true);
        // this.getRepositories(search, load);
        this.getRepositories(search);
      }
    },
    getCities() {
      return axios
        .post("web/trip/search", {
          from_id: this.selectedCity.place_id,
        })
        .then((response) => {
          // console.log(response.data.result)
          this.$store.commit("haveTickets", response.data.result);
        });
    },
    showNum(){
      console.log(this.phoneNumber);
    },
    // getRepositories: _.debounce(function(search, _load) {
    getRepositories: _.debounce(function(search) {
      // const load = _load;
      let URL = "web/settlement/popular";
      if (search) {
        let params = new URLSearchParams({
          name: search,
        });
        URL = "web/settlement?" + params.toString();
      }
      // console.log(search);
      // console.log(load);

      axios.get(URL).then((response) => {
        this.optionCity = response.data.result;
        // this.getCities().finally(() => load(false));
        this.getCities().finally(() => console.log('getrep'));
      });
    }, 850),
  },
};
</script>

