<script>
    import {mapState} from 'vuex'

    export default {
        name: "HomeTable",
        data() {
            return {
                lista: [
                    {"id": "1", "texto": "Час відправлення", filterName: 'timeDeparture', sortReverse: true},
                    {"id": "2", "texto": "Ціна", filterName: 'price', sortReverse: false},
                    {"id": "3", "texto": "Комфорт", filterName: 'comfort', sortReverse: false},
                    {"id": "4", "texto": "Час прибуття", filterName: 'timeArrival', sortReverse: false},
                    {"id": "5", "texto": "Час в дорозі", filterName: 'time', sortReverse: false},
                    {"id": "6", "texto": "Рейтинг", filterName: 'rating', sortReverse: false},
                    // {"id": "7", "texto": "За формуванням", filterName: 'price'}
                ],
                active_el: 1,
                //test
                obj: {
                    trip_id: '23',
                    from_id: '',
                    to_id: '',
                    date: '2020-10-29'
                },
                trip: {},
                loading: false,

                //    counter
                activeIndex: 0,
                counter: 1,
                //проміжні пункти
                isStop: false,

                isMobile: false,
            }
        },
        computed: {
            ...mapState([
                'objbuy',
                'ticketsbuy'
            ])
        },
        methods: {
            changeSelect(index, num) {
                // this.activeIndex === index ? this.activeIndex = 0 : this.activeIndex = index

                if(this.activeIndex === index){
                    this.ticketsbuy.reverse()
                    this.lista[num].sortReverse = !this.lista[num].sortReverse
                } else {
                    this.activeIndex = index
                    if(index.filterName === 'price'){
                        this.ticketsbuy.sort(function(a,b){
                            return parseFloat(a.price) - parseFloat(b.price)
                        })
                    } else if(index.filterName === 'comfort'){
                        this.ticketsbuy.sort(function(a,b){
                            return a.detail.bus.comfort.length - b.detail.bus.comfort.length
                        })
                    }
                    else if(index.filterName === 'timeDeparture'){
                        this.ticketsbuy.sort(function(a,b){
                            return a.departure_time - b.departure_time
                        })
                    } else if(index.filterName === 'timeArrival'){
                        this.ticketsbuy.sort(function(a,b){
                            return a.arrival_time - b.arrival_time
                        })
                    }  else if(index.filterName === 'rating'){
                        this.ticketsbuy.sort(function(a,b){
                            return a.detail.carrier.rating - b.detail.carrier.rating
                        })
                    }
                    // else if(index.filterName === 'time'){
                    //     this.ticketsbuy.sort(function(a,b){
                    //         return a.detail.bus.comfort.length - b.detail.bus.comfort.length
                    //     })
                    // }
                }
            },
            activate(el) {
                this.active_el = el;
            },
            incCounterBuy(item, index) {
                const counter = item.counterBuy || 1
                if (counter <= item.detail.route.free_seat) {
                    const tickets = this.ticketsbuy.slice()
                    tickets[index] = {
                        ...tickets[index],
                        counterBuy: counter + 1
                    }
                    this.$store.commit('haveTickets', tickets)
                }
            },
            decCounterBuy(item, index) {
                const counter = item.counterBuy || 1
                if(counter > 1) {
                    const tickets = this.ticketsbuy.slice()
                    tickets[index] = {
                        ...tickets[index],
                        counterBuy: counter - 1
                    }
                    this.$store.commit('haveTickets', tickets)
                }
            },
            handleClickTrip(index) {
                const cities = this.ticketsbuy.slice()
                cities[index] = {
                    ...cities[index],
                    isShow: !cities[index].isShow
                }

                //this.changeCity(cities) // get in props
                this.$store.commit('haveTickets', cities)
            },
            buyTicket(item) {
                this.obj.date = this.objbuy.date
                this.obj.trip_id = item.id
                this.obj.from_id = item.detail.route.from.id
                this.obj.to_id = item.detail.route.to.id
                // this.obj.ticketsCount = item.detail.route.to.id

                this.$store.commit('haveObj', this.obj)
                this.$router.push({name: 'FormTicket', params: {id: item.id}})
            },
            // onResize() {
            //     if (window.innerWidth > 600) {
            //         this.isMobile = false
            //     } else {
            //         this.isMobile = true
            //     }
            // },
            // sortSelected(){
            //     console.log(1)
            //     console.log(this.optionSelected)
            // }
        },
        created(){
            // window.addEventListener('resize', this.onResize)
            if (window.innerWidth > 576) {
                this.isMobile = false
            } else {
                this.isMobile = true
            }

            const cities = this.ticketsbuy.slice()
            cities.forEach(item => {
                item.isShow = false
            })

            this.activeIndex = this.lista[0]
        },
        // watch: {
        //     optionSelected:  function(){
        //             console.log('ggggg')
        //     }
        // }
        // beforeDestroy() {
        //     window.removeEventListener('resize', this.onResize)
        // },
    }
</script>

