import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/form-ticket/:id',
        name: 'FormTicket',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/FormTicket.vue')
    },
    {
        path: '/profile',
        name: 'Profile',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/Profile.vue')
    },
    {
        path: '/ticket-bus',
        name: 'TicketBus',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/TicketBus.vue')
    },
    {
        path: '/tickets',
        name: 'Tickets',
        component: () => import('../views/Tickets.vue')
    },
    {
        path: '/buytrip',
        name: 'BuyTrip',
        component: () => import('../views/BuyTrip.vue')
    },
    {
        path: '/publicoffer',
        name: 'PublicOffer',
        component: () => import('../views/PublicOffer.vue')
    },
    {
        path: '/about',
        name: 'About',
        component: () => import('../views/About.vue')
    },
]

const router = new VueRouter({
    mode: 'history',
    base: '/',
    routes
})

export default router
